.ratings {
  display: flex;
  justify-content: space-around;
  width: 15rem;
  &__circle {
    height: .7rem;
    width: .7rem;
    outline: solid 1px $color-pink-dark;
    outline-offset: .2rem;
    border-radius: 50%;

    &--fill {
    background-color: $color-pink-dark;
    }
  }
}
